@keyframes ani-mouse {
  0% {
    opacity: 1;
    top: 29%;
  }
  15% {
    opacity: 1;
    top: 50%;
  }
  50% {
    opacity: 0;
    top: 50%;
  }
  100% {
    opacity: 0;
    top: 29%;
  }
}

.home .scroll-btn {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 3;
  bottom: 0;
}

.home .scroll-btn > * {
  display: inline-block;
  line-height: 18px;
  font-size: 13px;
  font-weight: normal;
  color: #7f8c8d;
  color: #ffffff;
  font-family: "proxima-nova", "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 2px;
}

.home .scroll-btn > *:hover,
.home .scroll-btn > *:focus,
.home .scroll-btn > *.active {
  color: #fff;
}

.home .scroll-btn > *:hover,
.home .scroll-btn > *:focus,
.home .scroll-btn > *:active,
.home .scroll-btn > *.active {
  opacity: 0.8;
  filter: alpha(opacity=80);
}

.home .scroll-btn .mouse {
  position: relative;
  display: block;
  width: 35px;
  height: 55px;
  margin: 0 auto 20px;
  box-sizing: border-box;
  border: 3px solid white;
  border-radius: 23px;
}

.home .scroll-btn .mouse > * {
  position: absolute;
  display: block;
  top: 29%;
  left: 50%;
  width: 8px;
  height: 8px;
  margin: -4px 0 0 -4px;
  background: #fff;
  border-radius: 50%;
  animation: ani-mouse 2.5s linear infinite;
}

