  margin-bottom: 30px;
  color: #fff;
}

.s4__image {
  width: 100%;
  max-width: 334px;
  height: auto;
  transform: scale(1.3);
  opacity: 0;
}

.s4__list-title {
  font-size: 20px;
}

.s4__worning {
  color: #ff5f45;
  font-family: 'Lora', serif;
  font-weight: 400;
  font-style: italic;
  line-height: 24px;
  font-size: 17px;
}

.s6 {
  overflow: hidden;
  margin-top: 50px;
  padding-bottom: 50px;
}

.s6__relative {
  position: relative;
}

.s6__bg {
  display: block;
  position: absolute;
  top: -50px;
  right: 0;
  bottom: -50px;
  left: 40%;
  background-image: url("../img/s6-bg.png");
  background-size: cover;
  background-position: top right;
}

.s6__sub-title {
  text-transform: uppercase;
  color: #ffe5e3;
  display: inline-block;
  position: relative;
  font-family: 'Lora', serif;
  font-weight: 400;
  line-height: 24px;
  font-size: 16px;
  margin-bottom: 30px;
}

.s6__sub-title:after {
  content: '';
  display: block;
  position: absolute;
  bottom: -15px;
  left: 0;
  width: 40px;
  height: 4px;
  background: #ff5f45;
}

.s6__title {
  font-family: "ProximaExtraCLight", serif;
  line-height: 42px;
  font-size: 55px;
  margin-top: 100px;
  margin-bottom: 30px;
  color: #fff;
  color: #000;
}

.s6__white {
  z-index: 2;
  position: relative;
  background: #ffffff;
  margin-top: 80px;
  margin-right: -130px;
  padding: 70px 40px;
  transform: skewX(-12deg) translateX(40px);
  box-shadow: 6px 6px 16px rgba(0, 0, 0, 0.2);
}

.s6__white-back {
  transform: skewX(12deg);
}

.s6__list-content {
  font-family: "ProximaRegular", serif;
  font-size: 16px;
}

.s6__list-content-first {
  margin-bottom: 30px;
}

.s6__dark {
  z-index: 1;
  margin-right: -40px;
  margin-top: 94px;
  padding: 60px 40px 40px 200px;
  background: rgba(255, 255, 255, 0.8);
  transform: skewX(-12deg) translateX(20px) translateY(80px);
  box-shadow: 6px 6px 16px rgba(0, 0, 0, 0.2);
}

.s6__dark-back {
  transform: skewX(12deg);
}

.s6__dark-content {
  color: #fff;
  font-family: "ProximaBold", serif;
  font-size: 16px;
  color: #fb891b;
  display: block;
  position: relative;
}

.s6__dark-content:after, .s6__dark-content:before {
  display: block;
  position: absolute;
  color: #ff5f45;
  font-size: 60px;
  font-family: 'Lora', serif;
  font-style: italic;
}

.s6__dark-content:before {
  content: '„';
  top: -80px;
}

.s6__dark-content:after {
  content: '“';
  bottom: -60px;
  right: -10px;
}

.s6__author {
  font-family: "ProximaBold", serif;
  color: #000;
  font-size: 18px;
  margin-top: 20px;
}

.s6__left-1 {
  transform: translateX(150%);
  opacity: 1;
  z-index: 23;
  transition: all 1s;
}

.s6__right-1 {
  transform: translateX(-150%);
  opacity: 1;
  transition: all 1s;
}

